body {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #0F201C;
  background: linear-gradient(rgba(15, 32, 28, 0.7), rgba(15, 32, 28, 0.7)), url("../imgs/sa-login-bg.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  color: #fff;
}

#login {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: auto;
  padding: 0;
}
#login #login_error {
  color: #747474;
}
#login .message {
  color: #747474;
}
#login h1 a {
  width: 190px;
  height: 101px;
  background-size: 190px 101px;
  margin: 0;
}
#login h1 a:focus {
  box-shadow: none;
}
#login #loginform {
  margin: 40px 0;
  padding: 40px 30px;
  max-width: 320px;
  border-radius: 10px;
  border: none;
  box-shadow: 0px 2px 9px 0px rgba(0, 0, 0, 0.1);
}
#login #loginform label {
  color: #747474;
  font-size: 12px;
}
#login #loginform .input {
  border: none;
  border-radius: 0px;
  box-shadow: none;
  background-color: #fff;
  border-bottom: 2px solid #068C66;
  font-weight: normal;
  font-size: 22px;
  color: #0F201C;
  margin-bottom: 20px;
}
#login #loginform input[type=submit] {
  padding: 5px 20px;
  font-size: 12px;
  font-weight: bold;
  text-transform: uppercase;
  text-shadow: none;
  line-height: 1;
  border: none;
  border-radius: 50px;
  box-shadow: none;
  transition: all 0.3s ease-in-out;
  color: #fff;
  background-color: #068C66;
}
#login #loginform input[type=submit]:hover {
  background: #0F201C;
}
#login #backtoblog, #login #nav {
  text-align: center;
}
#login #backtoblog a, #login #nav a {
  color: #fff;
  font-weight: 600;
}
#login #nav {
  margin: 0 0 20px;
}
#login #backtoblog {
  margin: 0;
}
#login input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 30px white inset;
  -webkit-text-fill-color: #0F201C !important;
}

@media (max-width: 390px) {
  #login #loginform {
    margin-left: 10px;
    margin-right: 10px;
  }
}